import React from 'react';
import RangeSlider from '../components/rangeSlider/RangeSlider';
import { withRouter } from "react-router";
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';

class UserAge extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            next: false,
            age: 30
        }
    }

    componentWillUnmount = () => {console.log('componentWillUnmount')};
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('age');
    };

    componentDidUpdate = (oldProps, oldState) => { 
        console.log('componentDidUpdate '); 
        console.log(this.props)
        
        if (!this.state.next) {
            this.setState({
                next: true,
                age: this.props.age,
            });
        }
    };

    userAge = (age) => {
        this.setState({
            age,
            next: true
        });
    }

    userNextStep = () => {
        this.props.history.push("/user/growth");
        this.props.setDefirent('age', this.state.age);
    }

    render() { 
        return ( 
            <div className="user_age user_wr">
                <h2 className="user_wr__title "></h2>
                <div className="user_age__body">
                    <RangeSlider key={this.state.age} age={this.state.age} userAge={this.userAge}></RangeSlider>
                </div>
                <div className="user_age__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
         );
    }
}
 
export default withRouter(withSetDefinitionProgramm()(UserAge));