import {product_eaten_add, product_eaten_remove} from "./actions";

export const product_eaten_add_thunk = (prod, gram, typeMeal) => (dispatch, getState) => {
    const state = getState();
    let product_eaten = {
        ...state.statistic.product_eaten
    };
    let product = {...prod};

    product.calorie = +((prod.calorie / 100) * gram).toFixed(3);
    product.carb = +((prod.carb / 100) * gram).toFixed(3);
    product.fats = +((prod.fats / 100) * gram).toFixed(3);
    product.protein = +((prod.protein / 100) * gram).toFixed(3);

    product_eaten[typeMeal].push(product);

    dispatch(product_eaten_add(product_eaten));
}

export const product_eaten_remove_thunk = (id) => (dispatch, getState) => {
    const state = getState();

    let product_eaten = {};

    for(let key in state.statistic.product_eaten) {
        product_eaten[key] = [];
        state.statistic.product_eaten[key].forEach((prod) => {
            if (+prod.id !== +id) {
                product_eaten[key].push(prod);
            }
        });
    }

    dispatch(product_eaten_remove(product_eaten));
}


export const product_eaten_update_thunk = (prod, gram) => (dispatch, getState) => {
    const state = getState();
    let product = {...prod};

    product.calorie = +((prod.calorie / 100) * gram).toFixed(3);
    product.carb = +((prod.carb / 100) * gram).toFixed(3);
    product.fats = +((prod.fats / 100) * gram).toFixed(3);
    product.protein = +((prod.protein / 100) * gram).toFixed(3);

    let newproducts = state.statistics.product.map((prod) => {
        if (+prod.id === +product.id) {
            return product;
        } else {
            return prod;
        }
    });

    let statistics = {
        ...state.statistics,
        product: newproducts,
    };

    // dispatch(statistics_update(statistics));
}