import React from 'react';
import RangeSliderHalfToBe from '../components/rangeSlider/RangeSliderHalfToBe'; 
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';
import { withRouter } from "react-router";


class UserHalfToBe extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            halfToBe: 85,
            next: false,
        }
    }

    componentWillUnmount = () => {console.log('componentWillUnmount')};
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('halfToBe');
    };

    componentDidUpdate = (oldProps, oldState) => { 
        console.log('componentDidUpdate '); 

        if (!this.state.next) {
            this.setState({
                next: true,
                halfToBe: this.props.halfToBe,
            });
        }
    };

    userHalfToBe = (halfToBe) => {
        this.setState({
            halfToBe,
            next: true,
        });
    }

    userNextStep = () => {
        this.props.history.push("/user/lifestyle");
        this.props.setDefirent('halfToBe', this.state.halfToBe);
    }

    render() { 
        return ( 
            <div className="user_half user_wr">
                <h2 className="user_wr__title "> </h2>
                <div className="user_half__body">
                    <RangeSliderHalfToBe halfToBe={this.state.halfToBe} key={this.state.halfToBe} userHalfToBe={this.userHalfToBe}></RangeSliderHalfToBe>
                </div>
                <div className="user_half__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
         );
    }
}
 
export default withRouter(withSetDefinitionProgramm()(UserHalfToBe));