import React from 'react';
import { withRouter } from "react-router-dom";

class ProductsTypeList extends React.Component {

    openType = (e) => {
        this.props.history.push(`/products/${e.currentTarget.dataset.type}`)
    }

    shouldComponentUpdate(newProps, newState) {
        if (this.props.products === newProps.products) {
            return false
        }
        return true
    }

    componentDidUpdate() {
        console.log('componentDidUpdate- componentDidMount');
    }

    render() {
        const { products } = this.props;
        let productsTypeList = [];

        productsTypeList = products.map((prod) => {
            return (
                <div key={prod.name} data-type={prod.name} className="product_preview product_type_preview" onClick={this.openType}>
                    <h4 className="product_preview__title">{prod.name}</h4>
                    <p>{prod.count} </p>
                </div>
            );
        });

        return (
            <span>{productsTypeList}</span>
        );
    }
}

export default withRouter(ProductsTypeList);