import React from 'react';

class FilterPrevList extends React.Component {
    state = {
        filterBy: 'calorie',
        direction: 'up'
    }

    filterChange = (e) => {
        let filterBy = e.target.dataset.filter;
        let direction = this.state.direction;

        if (filterBy !== this.state.filterBy) {
            direction = 'up';
        } else if (this.state.direction === 'up') {
            direction = 'down';
        } else {
            direction = 'up';
        }

        this.setState({
            direction,
            filterBy
        }, () => {
            this.props.filterCall({ filterBy: filterBy, direction: direction })
        });

    }

    render() {
      
        let {filterBy, direction} = this.state;
        let classNameActive = 'filter__item filter__item_active';
        let classNameActiveUp = 'filter__item filter__item_active filter__item_active_up';


        return (
            <div className="filter">
                <span
                    className={filterBy === 'calorie' ? direction === 'up' ?  classNameActiveUp : classNameActive : 'filter__item'}
                    data-filter="calorie"
                    onClick={this.filterChange}
                ></span>

                <span
                    className={filterBy === 'protein' ? direction === 'up' ?  classNameActiveUp : classNameActive : 'filter__item'}
                    data-filter="protein"
                    onClick={this.filterChange}
                ></span>

                <span
                    className={filterBy === 'fats' ? direction === 'up' ?  classNameActiveUp : classNameActive : 'filter__item'}
                    data-filter="fats"
                    onClick={this.filterChange}
                ></span>

                <span
                    className={filterBy === 'carb' ? direction === 'up' ?  classNameActiveUp : classNameActive : 'filter__item'}
                    data-filter="carb"
                    onClick={this.filterChange}
                ></span>

            </div>
        );
    }
}

export default FilterPrevList;