import React from 'react';
import { connect } from 'react-redux';
import DiaryHead from './DiaryHead';
import ListEat from '../components/ListEat';
import ListSport from '../components/ListSport';

class Diary extends React.Component {
    componentDidUpdate = (oldProps, oldState) => { console.log('Diary- componentDidUpdate'); };
    componentDidMount = () => { console.log('Diary- componentDidMount'); };

    render() {
        const { diet, product_eaten, sports } = this.props;
        const carbPart = +diet.bmr / 4;
        const typeMeal = {
            breakfast: 'breakfast',
            dinner: 'dinner',
            supper: 'supper',
            snack: 'snack',
        }

        return (
            <div className="diary">
                <DiaryHead sports={sports}  diet={diet} product_eaten={product_eaten}></DiaryHead>
                <ListEat
                    title={''}
                    cal={carbPart}
                    icon={typeMeal.breakfast}
                    type={typeMeal.breakfast}
                    products={product_eaten[typeMeal.breakfast]}
                ></ListEat>
                <ListEat
                    title={''}
                    cal={carbPart}
                    icon={typeMeal.dinner}
                    type={typeMeal.dinner}
                    products={product_eaten[typeMeal.dinner]}
                ></ListEat>
                <ListEat
                    title={''}
                    cal={carbPart}
                    icon={typeMeal.supper}
                    type={typeMeal.supper}
                    products={product_eaten[typeMeal.supper]}
                ></ListEat>
                <ListEat
                    title={''}
                    cal={carbPart}
                    icon={typeMeal.snack}
                    type={typeMeal.snack}
                    products={product_eaten[typeMeal.snack]}
                ></ListEat>
                <ListSport
                    title={''}
                    icon={'sports'}
                    type={'sports'}
                    sports={sports}
                ></ListSport>
                
            </div>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        diet: state.user.info.diet,
        product_eaten: state.statistic.product_eaten,
        sports: state.statistic.sports,
    };
};

export default connect(mapStateToProps)(Diary);