import React from 'react';
import { withRouter } from "react-router";
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';

class UserGender extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            next: false,
            gender: null,
        }
    }

    componentWillUnmount = () => {console.log('componentWillUnmount')};
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('gender');
    };

    componentDidUpdate = (oldProps, oldState) => { 
        console.log('componentDidUpdate '); 

        if (!this.state.gender) {
            this.setState({
                next: true,
                gender: this.props.gender,
            });
        }
    };

    selectWomen = () => {
        this.setState({
            gender: '',
            next: true,
        });
    }

    selectMen = () => {
        this.setState({
            gender: '',
            next: true,
        });
    }

    userNextStep = () => {
        this.props.history.push("/user/age");
        this.props.setDefirent('gender', this.state.gender);
    }

    render() {
        const {gender, next} = this.state;

        return (
            <div className="user_sex user_wr">
                <h2 className="user_wr__title user_sex__title"> </h2>
                <div className="user_sex__body">
                    <button className={`btn btn_transp ${gender === '' ? 'user_sex__btn_active' : ''}`} onClick={this.selectWomen}></button>
                    <button className={`btn btn_transp ${gender === '' ? 'user_sex__btn_active' : ''}`} onClick={this.selectMen}></button>
                </div>
                <div className="user_sex__footer">
                    <button className={`btn btn_color1 ${!next ? 'user_sex__btn_disabled' : ''}`} disabled={!next} onClick={this.userNextStep}></button>
                </div>
            </div>
        );
    }
}

export default withRouter(withSetDefinitionProgramm()(UserGender));