import React from 'react';



let withSetDefinitionProgramm = () => Component => {

  class ComponentWithLocalStoreSave extends React.PureComponent {
    USER_DEFERENT_PROGRAMM_NAME = 'definitionProgramm';

    state = {
      combinedProps: { ...this.props },
    };

    setValueInLocalStore = (name, value) => {
      let newDefinitionProgrammObject = {};

      if (!localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) {
        newDefinitionProgrammObject[name] = value;
        localStorage.setItem(this.USER_DEFERENT_PROGRAMM_NAME, JSON.stringify(newDefinitionProgrammObject));
      } else {
        newDefinitionProgrammObject = { ...JSON.parse(localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) };
        newDefinitionProgrammObject[name] = value;
        localStorage.setItem(this.USER_DEFERENT_PROGRAMM_NAME, JSON.stringify(newDefinitionProgrammObject));
      }
    }

    gettDefirent = (name) => {
      if (localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) {
        let definitionProgramm = { ...JSON.parse(localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) };

        if (definitionProgramm[name]) {
          let newCombinedProps = { ...this.state.combinedProps, [name]: definitionProgramm[name] }
          this.setState({
            combinedProps: newCombinedProps
          });
        }
      }
    }


    render() {
      return <Component {...this.state.combinedProps} setDefirent={this.setValueInLocalStore} gettDefirent={this.gettDefirent} />;
    }

  }

  return ComponentWithLocalStoreSave;
}

export { withSetDefinitionProgramm };
