import React from 'react';
import { withRouter } from "react-router";
import Loader from "../components/Loader";

class UserTotal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
        }
    }
    USER_DEFERENT_PROGRAMM_NAME = 'definitionProgramm';

    componentDidMount = () => {
        console.log('componentDidMount');

        new Promise((resolve, reject) => {
            let definitionProgramm = { ...JSON.parse(localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) };
            resolve(definitionProgramm);
        }).then(data => {
            this.setState({
                data
            });
        });


    };

    userNextStep = () => {
        this.props.history.push("/user/diet");
    }

    render() {
        console.log(this.state.data)
        if (!this.state.data) {
            return <Loader></Loader>
        }
        let { gender, age, growth, half, halfToBe, userActive } = this.state.data;
        return (
            <div className="user_total user_wr">
                <h2 className="user_wr__title "></h2>
                <div className="user_total__body">
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"></div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{gender}</div>
                        </div>
                    </div>
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"></div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{age} </div>
                        </div>
                    </div>
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"> </div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{growth} </div>
                        </div>
                    </div>
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"></div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{half} </div>
                        </div>
                    </div>
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"></div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{halfToBe} </div>
                        </div>
                    </div>
                    <div className="user_total__row">
                        <div className="user_total__col">
                            <div className="user_total__txt"></div>
                        </div>
                        <div className="user_total__col">
                            <div className="user_total__txt user_total__txt_color">{userActive}</div>
                        </div>
                    </div>
                </div>
                <div className="user_total__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
        );
    }
}

export default withRouter(UserTotal);