import React from 'react';
import axios from 'axios';
import { withRouter } from "react-router-dom";
import { connect } from 'react-redux';
import { fetchProdAll } from '../../redux/loadData/fetchTunk';
import Loader from './Loader';
import SportPreview from './SportPreview';

class SportPrevList extends React.Component {
    // componentWillReceiveProps = (newProps) => { console.log('--ProductsPrevList- componentWillReceiveProps -'); };
    // componentWillUpdate = () => { console.log('--ProductsPrevList- componentWillUpdate'); };
    // componentDidUpdate = (oldProps, oldState) => { console.log('--ProductsPrevList- componentDidUpdate'); };
    // componentWillMount = () => { console.log('--ProductsPrevList- componentWillMount'); };
    // componentWillUnmount = () => { console.log('--ProductsPrevList- componentWillUnmount'); };
    state = {
        sports: []
    }
    componentWillMount() {
        console.log('--SportPrevList- componentDidMount');

        if (!this.state.sports.length) {
            axios.get('http://www.mocky.io/v2/5d0b83382f00007600e3f168?mocky-delay=1000ms')
            .then(function (response) {
                return response.data
            })
            .then((data) => {
                this.setState({
                    sports: data
                });
            });
        }
    }

   

    render() {
        const {sports} = this.state;

        if (!sports.length) {
            return <Loader />
        }

        let sportsItemList = sports.map((prod, key) => {
            return <SportPreview key={key} sport={prod}></SportPreview>
        });

        return (
            <span>
                {sportsItemList}
            </span>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        sports: state.loadData.sports,
    };
};

export default withRouter(connect(mapStateToProps)(SportPrevList));