import React from 'react';
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css';

class RangeSliderHalf extends React.Component {
  constructor (props, context) {
    super(props, context)
    this.state = {
      horizontal: this.props.growth,
    }
  }

  handleChangeHorizontal = value => {
    this.setState({
      horizontal: value
    })
  };

  onChangeComplete = () => {
    this.props.userGrowth(this.state.horizontal);
  }


  render () {
    const { horizontal } = this.state
    const horizontalLabels = {
      150: '150',
      175: '175',
      200: '200'
    }
    const formatcm = value => value + ' c';

    return (
      <div className='slider custom-labels'>
        <div className='value'>{formatcm(horizontal)}</div>
        <Slider
          min={150}
          max={200}
          value={horizontal}
          labels={horizontalLabels}
          onChangeComplete={this.onChangeComplete}
          onChange={this.handleChangeHorizontal}
        />
      </div>
    )
  }
}
  
  export default RangeSliderHalf