import React from 'react';
import { withRouter } from "react-router";
import { connect } from 'react-redux';
import Loader from "../components/Loader";
import { hbFormula, msgFormula, calorieNeeds, bzhuFormula } from './calculateDiet';
import { saveInfoInStore } from '../../redux/user/actions';
import {saveAndActivate} from '../../redux/user/thunkUser';

class UserDiet extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,

            form: {
                validForm: '',
                nameValid: '',
                passwordValid: '',
            }
        }
    }

    nameRef = React.createRef();
    passwordRef = React.createRef();

    USER_DEFERENT_PROGRAMM_NAME = 'definitionProgramm';

    componentDidMount = () => {
        console.log('componentDidMount');

        new Promise((resolve, reject) => {
            let definitionProgramm = { ...JSON.parse(localStorage.getItem(this.USER_DEFERENT_PROGRAMM_NAME)) };
            resolve(definitionProgramm);
        }).then(result => {
            let goal = 0;

            if (+result.half > +result.halfToBe) {
                goal = 'loss';
            } else {
                goal = 'gain';
            }

            let data = {
                age: +result.age,
                half: +result.half,
                halfToBe: +result.halfToBe,
                growth: +result.growth,
                gender: result.gender,
                lifestyle: +result.lifestyle,
                goal,
            }

            let bmr = hbFormula(data);
            let msg = msgFormula(data);
            let cn = calorieNeeds(data);
            let bzhu = bzhuFormula(bmr, msg, data.goal);

            this.setState({
                data,
                diet: {
                    bmr,
                    msg,
                    cn,
                    bzhu
                }
            });
        });


    };

    validationForm = () => {
        let name = this.nameRef.current.value;
        let password = this.passwordRef.current.value;
        let nameValid = '';
        let passwordValid = '';
        let validForm = true;

        if (!name.length) {
            nameValid = 'novalid';
            validForm = false;
        }
        if (!password.length) {
            nameValid = 'novalid';
            validForm = false;
        }

        this.setState({
            form: {
                nameValid,
                passwordValid,
                validForm,
            }
        }, () => {
            if (this.state.form.validForm) {
                this.submit();
            }
        })
    }

    userNextStep = () => {
        // this.props.dispatch(saveInfoInStore(this.state));
        this.validationForm();
    }

    submit = () => {
        const {data, diet} = this.state;
        data.name = this.nameRef.current.value;
        data.password = this.passwordRef.current.value;

        this.props.dispatch(saveAndActivate(data, diet));
        this.props.history.push("/diary");
    }

    render() {
        if (!this.state.data) {
            return <Loader></Loader>
        }
        const { diet } = this.state;
        let fullMg = diet.bzhu.protein + diet.bzhu.fats + diet.bzhu.carbohydrates;
        let proteinPercent = (diet.bzhu.protein * 100 / fullMg).toFixed(2) + '%';
        let fatsPercent = (diet.bzhu.fats * 100 / fullMg).toFixed(2) + '%';
        let carbPercent = (diet.bzhu.carbohydrates * 100 / fullMg).toFixed(2) + '%';

        return (
            <div className="user_diet user_wr">
                <h2 className="user_wr__title "> </h2>

                <div className="user_diet__body">
                    <h3 className="user_diet__description">   {diet.bmr} </h3>
                    <div className="user_diet__range">
                        <div className="user_diet__range_item user_diet__range_protein" style={{ width: proteinPercent }}></div>
                        <div className="user_diet__range_item user_diet__range_fats" style={{ width: fatsPercent }}></div>
                        <div className="user_diet__range_item user_diet__range_carb" style={{ width: carbPercent }}></div>
                    </div>
                    <div className="user_diet__table">
                        <div className="user_diet__table_row">
                            <div className="user_diet__table_col">
                                <div className="user_diet__name user_diet__name_protein"></div>
                            </div>
                            <div className="user_diet__table_col">
                                <div className="user_diet__percent"></div>
                                <div className="user_diet__mg">{diet.bzhu.protein} </div>
                            </div>
                        </div>
                        <div className="user_diet__table_row">
                            <div className="user_diet__table_col">
                                <div className="user_diet__name user_diet__name_fat"></div>
                            </div>
                            <div className="user_diet__table_col">
                                <div className="user_diet__percent"></div>
                                <div className="user_diet__mg">{diet.bzhu.fats} </div>
                            </div>
                        </div>
                        <div className="user_diet__table_row">
                            <div className="user_diet__table_col">
                                <div className="user_diet__name user_diet__name_carb"></div>
                            </div>
                            <div className="user_diet__table_col">
                                <div className="user_diet__percent"></div>
                                <div className="user_diet__mg">{diet.bzhu.carbohydrates} </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="user_diet__activate">
                    <div className="user_diet__activate_top">
                        <p></p>
                        <input ref={this.nameRef} type="text" placeholder="login"></input>
                        <input ref={this.passwordRef} type="password" placeholder="password"></input>
                    </div>
                </div>
                <div className="user_diet__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        state: state,
    };
};

export default withRouter(connect(mapStateToProps)(UserDiet));



