import React from 'react';
import UserGender from './UserGender';
import UserAge from './UserAge';
import UserGrowth from './UserGrowth';
import UserHalf from './UserHalf';
import UserHalfToBe from './UserHalfToBe';
import UserLifestyle from './UserLifestyle';
import UserTotal from './UserTotal';
import UserDiet from './UserDiet';
import UserActivate from './UserActivate';

import { Route } from 'react-router-dom';
import GoBackButton from '../components/GoBackButton';

class User extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sex: null,
        }
    }
    render() {
        return (
            <div className="user_test">
                <div className="user_test__back">
                    <GoBackButton></GoBackButton>
                </div>
                <Route path="/user/gender" component={UserGender} />
                <Route path="/user/age" component={UserAge} />
                <Route path="/user/growth" component={UserGrowth} />
                <Route path="/user/half" component={UserHalf} />
                <Route path="/user/halftobe" component={UserHalfToBe} />
                <Route path="/user/lifestyle" component={UserLifestyle} />
                <Route path="/user/total" component={UserTotal} />
                <Route path="/user/diet" component={UserDiet} />
                <Route path="/user/activate" component={UserActivate} />
            </div>
        );
    }
}

export default User;