import React from 'react';
import ReactDOM from 'react-dom';

class WhereSaveCard extends React.Component {
    componentWillMount() {
        this.root = document.createElement('div');
        this.root.className = 'modal';
        document.body.appendChild(this.root);
    }
    componentWillUnmount() {
        document.body.removeChild(this.root);
    }

    changeType = (e) => {
        this.props.addProduct(e.target.dataset.typesave);
    }

    render() { 
        return ReactDOM.createPortal(
            <div className="list_type">
                <p onClick={this.changeType} data-typesave="breakfast"></p>
                <p onClick={this.changeType} data-typesave="dinner"></p>
                <p onClick={this.changeType} data-typesave="supper"></p>
                <p onClick={this.changeType} data-typesave="snack"></p>
            </div>
            
            ,
            this.root
        )
        
    }
}
 
export default WhereSaveCard;