import React from 'react';
import { withRouter } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { Progress } from 'react-sweet-progress';
import "react-sweet-progress/lib/style.css";


class ListSport extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            percent: 0,
            fullCarb: 0,
            oneProcentCarb: 0,
        }
    }

    componentDidMount() {
        
    }

    openTypeMeal = () => {
        this.props.history.push(`/sports`);
    }

    render() {
        let {sports} = this.props;
        let fullCal = 0;
        if (sports.length) {
            sports.forEach((sport) => {
                fullCal += +sport.cal
            });
        }

        return (
            <div className="list_eat" onClick={this.openTypeMeal}>
                <div className={`list_eat__icon ${this.props.icon}`}></div>
                <div className="list_eat__middle">
                    <div className="list_eat__title">{this.props.title}</div>
                    <div className="list_eat__progress">
                        <div className="list_eat__cal">
                            <span>{fullCal} </span>
                            /
                            <span>  </span>
                        </div>
                        <Progress
                            percent={0}
                            strokeWidth={10}
                            theme={{
                                success: {
                                    color: 'red'
                                },
                                active: {
                                    color: '#63db9d'
                                },
                                default: {
                                    color: 'red'
                                }
                            }}
                        />
                    </div>
                </div>
                <div className="list_eat__add">
                    <FontAwesomeIcon color="#000" size="lg" icon={faPlus} />
                </div>
            </div>
        );
    }
}

export default withRouter(ListSport);
