import React from 'react';
import { withRouter } from "react-router";
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';

class UserLifestyle extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            lifestyle: 0,
            next: false,
        }
    }

    componentWillUnmount = () => { console.log('componentWillUnmount') };
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('lifestyle');
    };

    componentDidUpdate = (oldProps, oldState) => {
        console.log('componentDidUpdate ');

        if (!this.state.next) {
            this.setState({
                next: true,
                lifestyle: this.props.lifestyle,
            });
        }
    };

    userNextStep = () => {
        this.props.history.push("/user/total");
        this.props.setDefirent('lifestyle', this.state.lifestyle);
    }

    onClick = (e) => {
        this.setState({
            lifestyle: e.target.getAttribute('data-active'),
            next: true,
        });
    }

    render() {
        const { lifestyle, next } = this.state;
        return (
            <div className="user_activite user_wr">
                <h2 className="user_wr__title "> </h2>
                <div className="user_activite__body">

                    <button className={`user_activite__button btn btn_transp ${lifestyle === '1.2' ? 'user_activite__button_active' : ''}`} data-active="1.2" onClick={this.onClick}>
                        
                    </button>
                    <button className={`user_activite__button btn btn_transp ${lifestyle === '1.375' ? 'user_activite__button_active' : ''}`} data-active="1.375" onClick={this.onClick}>
                        
                    </button>
                    <button className={`user_activite__button btn btn_transp ${lifestyle === '1.55' ? 'user_activite__button_active' : ''}`} data-active="1.55" onClick={this.onClick}>
                        
                    </button>
                    <button className={`user_activite__button btn btn_transp ${lifestyle === '1.725' ? 'user_activite__button_active' : ''}`} data-active="1.725" onClick={this.onClick}>
                         
                    </button>

                </div>

                <div className="user_activite__footer">
                    <button className={`btn btn_color1 ${!next ? 'btn_disabled' : ''}`} disabled={!next} onClick={this.userNextStep}></button>
                </div>
            </div>
        );
    }
}

export default withRouter(withSetDefinitionProgramm()(UserLifestyle));