import React, { Component } from 'react';
import { connect } from 'react-redux';
import SportPreview from './SportPreview';
import GoBackButton from './GoBackButton';

class SportsPreview extends Component {

    openSportsList = () => {
        console.log(this.props)
        this.props.history.push('/sports/list');
    }

    render() {
        
        let sportsListPreview = [];

        if (this.props.sports.length) {
            sportsListPreview = this.props.sports.map((v, k) => {
                return <SportPreview key={k} sport={v} ></SportPreview>
            });
        } else {
            sportsListPreview = (<div> e </div>);
        }

        return (
            <div className="edit_eat">
                <GoBackButton></GoBackButton>
                <div className="edit_eat__header">
                    <h3 className="edit_eat__title"></h3>
                </div>
                <div className="edit_eat__body">
                    {sportsListPreview}
                </div>
                <button onClick={this.openSportsList}></button>
            </div>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        sports: state.statistic.sports,
    };
};

export default connect(mapStateToProps)(SportsPreview);