import axios from 'axios';
import { setResponsProducts, setLoadProductType } from "./actions";

function fetchProdAll(dispatch) {
    return function () {
        axios.get('http://www.mocky.io/v2/5d0545263200006a00d78c68?mocky-delay=1000ms')
            .then(function (response) {
                return response.data
            })
            .then((data) => {
                dispatch(setResponsProducts(data));
            })
    }
}
function loadProductType(dispatch) {
    return function () {
        axios.get('http://www.mocky.io/v2/5d0545263200006a00d78c68')
            .then(function (response) {
                let productType = [];

                for (const key in response.data) {
                    productType.push({
                        name: key,
                        count: response.data[key].length
                    })
                }

                return productType
            })
            .then((data) => {
                dispatch(setLoadProductType(data));
            })
    }
}
function getProductBtId(id) {
    return axios.get('http://www.mocky.io/v2/5d07ec723400004d005d959e')
        .then(function (response) {
            return response.data
        })
        .then((data) => {
            let product = null;
            for (let key = 0; key < data.length; key++) {
                if (+data[key].id === +id) {
                    product = data[key];
                    break
                }
            }
            return product;
        });
}

export { fetchProdAll, loadProductType, getProductBtId }
