import React from 'react';
import Navigation from './components/Navigation.js';
import MainViews from './homepage/MainViews.js';
import User from './user/User.js';
import Activate from './components/Activate';

import { connect } from 'react-redux';
import { Route, Redirect } from 'react-router-dom';
import { saveInfoInStore } from '../redux/user/actions';

import '../assets/css/main.sass';

class App extends React.Component {

  USERS_ACTIVE = 'USERS_ACTIVE';


  componentWillMount() {
    let userActive = localStorage.getItem(this.USERS_ACTIVE)

    if (userActive) {
      let user = JSON.parse(localStorage.getItem(this.USERS_ACTIVE));
      this.props.dispatch(saveInfoInStore({ info: user.info, diet: user.diet }))
    }
  }

  render() {
    const { auth } = this.props;
    let user = !!localStorage.getItem(this.USERS_ACTIVE);

    if (!auth) {
      return (
        <React.Fragment>
          {!user ? <Redirect to={'/activate'} /> : null}
          <Route path="/activate" component={Activate} />
          <Route path="/user/:param" component={User} />
        </React.Fragment>
      );
    }

    return (
      <div className="App">
        
        <MainViews></MainViews>
        <Navigation></Navigation>
      </div>
    )
  }
}

const mapStateToProps = function (state) {
  return {
    auth: state.user.login.auth,
  };
};

export default connect(mapStateToProps)(App);