import React from 'react';
import { withRouter } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { Progress } from 'react-sweet-progress';
import "react-sweet-progress/lib/style.css";


class ListEat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            percent: 0,
            fullCarb: 0,
            oneProcentCarb: 0,
        }
    }

    componentDidMount() {
        let fullCarb = 0;
        let oneProcentCarb = this.props.cal / 100;

        if (this.props.products.length) {
            this.props.products.forEach((product) => {
                fullCarb += product.calorie;
            });
        }

        this.setState({
            oneProcentCarb,
            fullCarb,
        });
    }

    openTypeMeal = () => {
        this.props.history.push(`/typemeal/${this.props.type}`);
    }

    render() {
        const { fullCarb, oneProcentCarb } = this.state;
        let percent = Math.round(fullCarb  / oneProcentCarb);
        let classActive = '#fbc630';

        if (+percent > 100) {
            percent = 100;
            classActive = '#fb7272';
        } 

        return (
            <div className="list_eat" onClick={this.openTypeMeal}>
                <div className={`list_eat__icon ${this.props.icon}`}></div>
                <div className="list_eat__middle">
                    <div className="list_eat__title">{this.props.title}</div>
                    <div className="list_eat__progress">
                        <div className="list_eat__cal">
                            <span>{fullCarb} </span>
                            /
                            <span> {this.props.cal} </span>
                        </div>
                        <Progress
                            percent={percent}
                            strokeWidth={10}
                            theme={{
                                success: {
                                    color: classActive
                                },
                                active: {
                                    color: '#63db9d'
                                },
                                default: {
                                    color: 'red'
                                }
                            }}
                        />
                    </div>
                </div>
                <div className="list_eat__add">
                    <FontAwesomeIcon color="#000" size="lg" icon={faPlus} />
                </div>
            </div>
        );
    }
}

export default withRouter(ListEat);
