function hbFormula(data) {
	let bmr;
	if (data.gender === '') {
		bmr = 447.593 + data.half * 9.247 + data.growth * 3.098 - data.age * 4.330;
	} else {
		bmr = 88.362 + data.half * 13.397 + data.growth * 4.799 - data.age * 5.677;
	}
	return Math.ceil(bmr * data.lifestyle);
}

function msgFormula(data) {
	var msg;

	if (data.gender === '') {
		msg = 10 * data.half + 6.5 * data.growth - 5 * data.age - 161;
	} else {
		msg = 10 * data.half + 6.25 * data.growth - 5 * data.age + 5;
	}

	return Math.ceil(msg * data.lifestyle);
}

function calorieNeeds(data) {
	var cn;

	switch (data.lifestyle) {
		case 1.2:
			cn = (data.half * 26) + ' - ' + (data.half * 30);
			break;
		case 1.375:
			cn = (data.half * 31) + ' - ' + (data.half * 37);
			break;
		case 1.55:
			cn = (data.half * 38) + ' - ' + (data.half * 40);
			break;
		case 1.725:
			cn = (data.half * 41) + ' - ' + (data.half * 50);
			break;
		case 1.9:
			cn = (data.half * 50) + ' - ' + (data.half * 55);
			break;
		default:
			break;
	}
	return cn;
}

function bzhuFormula(bmr, msg, goal) {
	var average = Math.ceil((bmr + msg) / 2);
	var pfc = average;

	var bzhu = {
		protein: Math.ceil((pfc * 0.28) / 4),
		fats: Math.ceil((pfc * 0.33) / 9),
		carbohydrates: Math.ceil((pfc * 0.4) / 4)
	};

	switch (goal) {
		case "loss":
			bzhu.calories = Math.ceil(average * 0.8) + ' - ' + Math.ceil(average * 0.9);
			pfc = average * 0.85;
			bzhu.goal = '';
			break;
		case "gain":
			bzhu.calories = Math.ceil(average * 1.1) + ' - ' + Math.ceil(average * 1.2);
			pfc = average * 1.15;
			bzhu.goal = '';
			break;
		case "keep":
			bzhu.calories = Math.ceil(average * 0.95) + ' - ' + Math.ceil(average * 1.05);
			pfc = average;
			bzhu.goal = '';
			break;
		default:
			break;
	}


	bzhu.protein = Math.ceil((pfc * 0.28) / 4);
	bzhu.fats = Math.ceil((pfc * 0.33) / 9);
	bzhu.carbohydrates = Math.ceil((pfc * 0.4) / 4);

	return bzhu;
}

export { hbFormula, msgFormula, calorieNeeds, bzhuFormula }
