import React from 'react';
import { connect } from 'react-redux';
import { userCheck } from '../../redux/user/thunkUser';
// import { saveInfoInStore } from '../../redux/user/actions';

class Activate extends React.Component {
    nameRef = React.createRef();
    passwordRef = React.createRef();

    state = {
        form: {
            validForm: '',
            nameValid: '',
            passwordValid: '',
        }
    }

    newAccoutn = () => {
        this.props.history.push("/user/gender");
    }
    login = () => {
        let name = this.nameRef.current.value;
        let password = this.passwordRef.current.value;

        const USERS_INFO = 'USERS_INFO';
        let users = [];
        let userValid = {};
        let userActivate = false;

        if (localStorage.getItem(USERS_INFO)) {
            users = JSON.parse(localStorage.getItem(USERS_INFO));
        } 
      
        users.forEach((user) =>{

            if ((user.info.name.trim() === name.trim()) && (user.info.password === password)) {
                userActivate = true;
                userValid = user;
            }
        });

        if (userActivate) {
            this.props.dispatch(userCheck({info: userValid.info, diet: userValid.diet}));
            this.props.history.push("/diary");
        } else {
            this.setState({
                form: {
                    ...this.state.form,
                    passwordValid: 'novalid',
                    nameValid: 'novalid',
                }
            })
        }
        

        // 
    }

    validationForm = () => {
        let name = this.nameRef.current.value;
        let password = this.passwordRef.current.value;
        let nameValid = '';
        let passwordValid = '';
        let validForm = true;

        if (!name.length) {
            nameValid = 'novalid';
            validForm = false;
        }
        if (!password.length) {
            passwordValid = 'novalid';
            validForm = false;
        }

        this.setState({
            form: {
                nameValid,
                passwordValid,
                validForm,
            }
        }, () => {
            if (this.state.form.validForm) {
                this.login();
            }
        })
    }



    render() {
        return (
            <div className="activate">
                <div className="activate_top">
                    <p></p>
                    <input className={this.state.form.nameValid.length ? 'error' : ''} ref={this.nameRef} type="text" placeholder="login"></input>
                    <input className={this.state.form.passwordValid.length ? 'error' : ''} ref={this.passwordRef} type="password" placeholder="password"></input>
                    <div className="item button-hand">
                        <button onClick={this.validationForm} className=""> <div className="hands"></div></button>
                    </div>
                </div>
                <div className="activate_bot">
                    <div className="item button-hand">
                        <button onClick={this.newAccoutn} className="">  <div className="hands"></div></button>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect()(Activate);