import ACTIONS from "../constants/actions";

const initialState = {
    product_eaten: {
        breakfast: [
            // {
            //     "name": " ",
            //     "type": "",
            //     "calorie": 72,
            //     "protein": 5,
            //     "fats": 0.2,
            //     "carb": 40,
            //     "id": 2
            // },
        ],
        dinner: [],
        supper: [],
        snack: [],
    },
    sports: [
        // {
        //     name: ' ',
        //     time: 30,
        //     cal: 195,
        //     id: 1
        // }
    ]
    

}

export default (state = initialState, action) => {
    switch (action.type) {
        case ACTIONS.PRODUCT_EATEN.ADD:
            return {
                ...state,
                product_eaten: action.payload
            }
        case ACTIONS.PRODUCT_EATEN.REMOVE:
            return {
                ...state,
                product_eaten: action.payload
            }
        case ACTIONS.SPORTS.SPORTS_ADD:
            return {
                ...state,
                sports: [
                    ...state.sports,
                    action.payload
                ]
            }
        default:
            return state
    }
}