import React, { Component } from "react";
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClipboardList, faFish, faPlus, faChartBar, faUser } from '@fortawesome/free-solid-svg-icons'

class Navigation extends Component {
  
  
    render() {
        return (
            <div className="bottom_panel">
                <NavLink to="/diary" exact className="bottom_panel__link" activeClassName="bottom_panel__link-active"> <FontAwesomeIcon color="#bababa" size="lg" icon={faClipboardList} /></NavLink>
                <NavLink to="/products"  className="bottom_panel__link" activeClassName="bottom_panel__link-active"> <FontAwesomeIcon color="#bababa" icon={faFish}  size="lg" /></NavLink>
                <NavLink to="/add" exact className="bottom_panel__link bottom_panel__link_add" activeClassName=""><FontAwesomeIcon color="#bababa" icon={faPlus}  size="lg" /></NavLink>
                <NavLink to="/result" exact className="bottom_panel__link" activeClassName="bottom_panel__link-active">e<FontAwesomeIcon color="#bababa" icon={faChartBar}  size="lg" /></NavLink>
                <NavLink to="/profile" exact className="bottom_panel__link" activeClassName="bottom_panel__link-active"><FontAwesomeIcon color="#bababa" icon={faUser}  size="lg" /></NavLink>
            </div>
        );
    }
}

export default Navigation;