import React from 'react';
import { Route } from 'react-router-dom';
import Diary from '../diary/Diary';
import TypeMealPreview from '../components/TypeMealPreview';
import ProductCard from '../components/ProductCard';
import SportCard from '../components/SportCard';
import Products from '../components/Products';
import ProductsPrevList from '../components/ProductsPrevList';
import SportsPrevList from '../components/SportsPrevList';
import HeaderApp from '../components/HeaderApp';
import SportsPreview from '../components/SportsPreview';
import Profile from '../components/Profile';


class MainViews extends React.Component {
  componentWillReceiveProps = (newProps) => { console.log('MainViews- componentWillReceiveProps -'); };
  componentWillUpdate = () => { console.log('MainViews- componentWillUpdate'); };
  componentDidUpdate = (oldProps, oldState) => { console.log('MainViews- componentDidUpdate'); };

  componentDidMount = () => { console.log('MainViews- componentDidMount'); };
  componentWillUnmount = () => { console.log('MainViews- componentWillUnmount'); };


  render() {
    return (
      <div className="page">
        <Route path="/diary" component={HeaderApp} />
        <Route path="/diary" component={Diary} />
        <Route path="/typemeal/:type" component={TypeMealPreview} />
        <Route exact path="/sports" component={SportsPreview} />
        <Route exact path="/sports/list" component={SportsPrevList} />

        <Route exact path="/sport/:id" component={SportCard} />
        <Route exact path="/product/:id" component={ProductCard} />
        <Route exact path="/products" component={Products} />
        <Route exact path="/products/:type" component={ProductsPrevList} />
        <Route exact path="/profile" component={Profile} />
      </div>
    );
  }
}

export default MainViews;