import ACTIONS from "../constants/actions";

const initialState = {
    login: {
        auth: false,
    },
    info: {
        data: {
            age: 0,
            gender: "",
            goal: "",
            growth: 0,
            half: 0,
            halfToBe: 0,
            lifestyle: 0
        },
        diet: {
            bmr: 0,
            bzhu: {
                calories: "",
                carbohydrates: 0,
                fats: 0,
                goal: "",
                protein: 0,
            },
            cn: "",
            msg: 0,
        }
    },
}

export default (state = initialState, action) => {
    switch (action.type) {
        case ACTIONS.USER.SAVE_INFO_IN_STORE:
            return {
                ...state,
                login: {
                    auth: true
                },
                info: {
                    data: action.payload.info,
                    diet: action.payload.diet,
                }
            }
        case ACTIONS.USER.LOGOUT:
            const USERS_ACTIVE = 'USERS_ACTIVE';
            localStorage.removeItem(USERS_ACTIVE);
            return {
                ...state,
                login: {
                    auth: false
                }
            }
        default:
            return state
    }
}