import React from 'react';

class ControllProductCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    addProd = () => {
        this.props.addProduct()
    }
    removeProduct = () => {
        this.props.removeProduct()
    }
    render() { 
        return ( 
            <div className="controllCard">
                {this.props.productAdded ?  <button onClick={this.props.updateProduct}></button> : <button onClick={this.addProd}></button>}
                {this.props.productAdded && <button onClick={this.removeProduct}></button>}
            </div>
        );
    }
}
 
export default ControllProductCard;