import React from 'react';
import { connect } from 'react-redux';
import Loader from '../components/Loader';
import { loadProductType } from '../../redux/loadData/fetchTunk';
import ProductsTypeList from './ProductsTypeList';

class Products extends React.Component {
    componentDidUpdate = (oldProps, oldState) => { console.log('Products- componentDidUpdate'); };

    shouldComponentUpdate(newProps, newState) {
        if (this.props.productType === newProps.productType) {
            return false
        }
        return true
    }

    componentWillMount() {
        if (!this.props.productType) {
            this.props.dispatch(loadProductType(this.props.dispatch));
        }
    }

    componentDidMount() {
        console.log('Products- componentDidMount');
    }

    render() {
        if (!this.props.productType) {
            return <Loader />
        }

        const { productType } = this.props;

        return (
            <span>
                <ProductsTypeList products={productType}></ProductsTypeList>
            </span>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        productType: state.loadData.products.productType,
    };
};

export default connect(mapStateToProps)(Products);