import React from 'react';
import { withRouter } from "react-router-dom";
import { connect } from 'react-redux';
import ControllProductCard from './ControllProductCard';
import { getProductBtId } from '../../redux/loadData/fetchTunk';
import Loader from '../components/Loader';
// import {updateStatAddTH, updateStatRemoveTH, updateStatUpdateTH} from '../../redux/statistics/thunkStatistics';
import GoBackButton from '../components/GoBackButton';
import WhereSaveCard from '../components/WhereSaveCard';
import {product_eaten_add_thunk, product_eaten_remove_thunk} from '../../redux/statistic/thunkProdEat';

class ProductCard extends React.Component {
    state = {
        product: null,
        productAdded: false,

        opensavecard: false,
    }

    inputRef = React.createRef();
    

    componentWillMount() {
        let idProd = this.props.match.params.id.split('-')[1];

        getProductBtId(idProd).then((product) => {
            let productAdded = false;
            let prodBe = this.props.product_eaten;

            for (let i in prodBe) {
                for(let j = 0; j < prodBe[i].length; j++) {
                    if (+prodBe[i][j].id === +this.props.match.params.id.split('-')[1]) {
                        productAdded = true;
                        break;
                    }
                }
            }

            this.setState({
                product,
                productAdded
            });
        })
    }

    componentDidMount() {
        console.log('----ProductCard - componentDidMount')
        
    }

    componentDidUpdate(oldProps, oldState) {
        console.log('----ProductCard - componentDidUpdate')
    }

    openChangeWhereSave = () => {
        this.setState({
            opensavecard: true
        });
    }

    addProduct = (typeMeal) => {
        let prod = this.state.product;
        let gram = this.inputRef.current.value;
        this.props.dispatch(product_eaten_add_thunk(prod, gram, typeMeal))
        this.setState({
            opensavecard: false
        });
        this.props.history.push(`/diary`);
    }

    removeProduct = () => {
        let idProd = this.state.product.id;
        this.props.dispatch(product_eaten_remove_thunk(idProd));
        this.props.history.push(`/diary`);
    }

    updateProduct = () => {
        // let prod = this.state.product;
        // let gram = this.inputRef.current.value;
        // this.props.dispatch(updateStatUpdateTH(prod, gram));
    }

    render() {
        if (!this.state.product) {
            return <Loader/>
        }

        const { productAdded, product } = this.state;

        return (
            <div className="product_full">
                <GoBackButton></GoBackButton>
                <div className="product_full__header">
                    <h2 className="product_full__title">{product.name}</h2>
                </div>
                <div className="product_full__body">
                    <input type="number" placeholder="100 " defaultValue='100' ref={this.inputRef}></input> <span>.</span>
                    <ControllProductCard updateProduct={this.updateProduct} removeProduct={this.removeProduct} addProduct={this.openChangeWhereSave}  productAdded={productAdded}></ControllProductCard>
                </div>
                <div className="product_full__footer">
                    <span className="product_preview__text">{product.calorie} </span>
                    <span className="product_preview__text"> {product.protein} .</span>
                    <span className="product_preview__text"> {product.fats} .</span>
                    <span className="product_preview__text"> {product.carb} .</span>
                </div>
                {this.state.opensavecard && <WhereSaveCard addProduct={this.addProduct} />}
            </div>
        );
    }
}
const mapStateToProps = function (state) {
    return {
        product_eaten: state.statistic.product_eaten,
        products: state.products,
    };
};

export default withRouter(connect(mapStateToProps)(ProductCard));