import React from 'react';
import RangeSliderGrowth from '../components/rangeSlider/RangeSliderGrowth';
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';
import { withRouter } from "react-router";


class UserGrowth extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growth: 175,
            next: false,
        }
    }

    componentWillUnmount = () => {console.log('componentWillUnmount')};
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('growth');
    };

    componentDidUpdate = (oldProps, oldState) => { 
        console.log('componentDidUpdate '); 
        
        if (!this.state.next) {
            this.setState({
                next: true,
                growth: this.props.growth,
            });
        }
    };

    userGrowth = (growth) => {
        this.setState({
            growth,
            next: true
        });
    }

    userNextStep = () => {
        this.props.history.push("/user/half");
        this.props.setDefirent('growth', this.state.growth);
    }


    render() {
        return (
            <div className="user_half user_wr">
                <h2 className="user_wr__title "></h2>
                <div className="user_half__body">
                    <RangeSliderGrowth growth={this.state.growth} key={this.state.growth} userGrowth={this.userGrowth}></RangeSliderGrowth>
                </div>
                <div className="user_half__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
        );
    }
}

export default withRouter(withSetDefinitionProgramm()(UserGrowth));