import React from 'react';
// import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUtensils, faRunning } from '@fortawesome/free-solid-svg-icons';
import { Progress } from 'react-sweet-progress';
import "react-sweet-progress/lib/style.css";

class DiaryHead extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            onePercentProtein: this.props.diet.bzhu.protein / 100,
            onePercentFats: this.props.diet.bzhu.fats / 100,
            onePercentCarb: this.props.diet.bzhu.carbohydrates / 100,
            onePercentCal: this.props.diet.bmr / 100,
        }
    }

    render() {
        const { diet, product_eaten, sports } = this.props;

        let calBurn = 0;
        if (sports.length) {
            sports.forEach((sport) => {
                calBurn += +sport.cal;
            });
        }


        let stat = {
            calorie: 0,
            carb: 0,
            fats: 0,
            protein: 0,
        }

        for (let key in product_eaten) {
            product_eaten[key].forEach((prod) => {
                stat.calorie += prod.calorie;
                stat.carb += prod.carb;
                stat.fats += prod.fats;
                stat.protein += prod.protein;
            });
        }
        

        let precentProtein = Math.ceil(stat.protein / this.state.onePercentProtein);
        let precentFats = Math.ceil(stat.fats / this.state.onePercentFats);
        let precentCarb = Math.ceil(stat.carb / this.state.onePercentCarb);
        let precentCal = Math.ceil((stat.calorie) / this.state.onePercentCal);

        return (
            <div className="diary__head">
                <div className="diary__head_row">
                    <div className="diary__head_col diary__head_col-2 flex flex_column">
                        <div className="diary__head_icon">
                            <FontAwesomeIcon color="lavender" size="lg" icon={faUtensils} />
                        </div>
                        <div className="diary__head_ieat diary__head_count">{stat.calorie}</div>
                        <div className="diary__head_text"></div>
                    </div>
                    <div className="diary__head_col diary__head_col-8 flex">
                        <div className="diary__head_circle">
                            <span className="diary__head_cal flex flex_column">
                                <span className="diary__head_cal_remain">{diet.bmr + calBurn - (stat.calorie)}</span>
                                 
                            </span>
                            <Progress
                                type="circle"
                                percent={precentCal}
                                width={180}
                                theme={{
                                    success: {
                                        color: [precentCal > 100 ? '#fb7272' : '#fbc630']
                                    },
                                    active: {
                                        color: '#ececec'
                                    },
                                    default: {
                                        color: '#fbc630'
                                    }
                                }}
                            />
                        </div>

                    </div>
                    <div className="diary__head_col diary__head_col-2 flex flex_column">
                        <div className="diary__head_icon">
                            <FontAwesomeIcon color="lavender" size="lg" icon={faRunning} />
                        </div>
                        <div className="diary__head_ieat diary__head_count">{calBurn}</div>
                        <div className="diary__head_text"></div>
                    </div>
                </div>
                <div className="diary__head_row">
                    <div className="diary__head_col diary__head_col-4 flex flex_column">
                        <div className="diary__head_text"></div>
                        <div className="diary__head_progress">
                            <Progress
                                percent={precentProtein}
                                theme={{
                                    success: {
                                        color: [precentProtein > 100 ? '#fb7272' : '#fbc630']
                                    },
                                    active: {
                                        color: '#63db9d'
                                    },
                                    default: {
                                        color: '#fbc630'
                                    }
                                }}
                            />
                        </div>
                        <div className="diary__head_ ">{stat.protein} / {diet.bzhu.protein} . </div>
                    </div>
                    <div className="diary__head_col diary__head_col-4 flex flex_column">
                        <div className="diary_head_text"></div>
                        <div className="diary__head_progress">
                            <Progress
                                percent={precentFats > 100 ? 100 : precentFats}
                                theme={{
                                    success: {
                                        color: [precentFats > 100 ? '#fb7272' : '#fbc630']
                                    },
                                    active: {
                                        color: '#63db9d'
                                    },
                                    default: {
                                        color: '#fbc630'
                                    }
                                }}
                            />
                        </div>
                        <div className="diary__head_ ">{stat.fats} / {diet.bzhu.fats} . </div>
                    </div>
                    <div className="diary__head_col diary__head_col-4 flex flex_column">
                        <div className="diary__head_text"></div>
                        <div className="diary__head_progress">
                            <Progress
                                percent={precentCarb}
                                theme={{
                                    success: {
                                        color: [precentCarb > 100 ? '#fb7272' : '#fbc630']
                                    },
                                    active: {
                                        color: '#63db9d'
                                    },
                                    default: {
                                        color: '#fbc630'
                                    }
                                }}
                            />
                        </div>
                        <div className="diary__head_ ">{stat.carb} / {diet.bzhu.carbohydrates} .</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default DiaryHead;