import React, { Component } from 'react';
import { connect } from 'react-redux';
import ProductPreview from './ProductPreview';
import GoBackButton from './GoBackButton';

class TypeMealPreview extends Component {

    render() {
        // console.log(this.props);

        const { type } = this.props.match.params;
        const productEatenOnThisType = this.props.product_eaten[type];
        let productListPreview = [];

        if (productEatenOnThisType.length) {
            productListPreview = productEatenOnThisType.map((v, k) => {
                return <ProductPreview key={k} product={v} ></ProductPreview>
            });
        } else {
            productListPreview = (<div className="a"> e </div>);
        }

        return (
            <div className="edit_eat">
                <GoBackButton></GoBackButton>
                <div className="edit_eat__header">
                    <h3 className="edit_eat__title">{type}</h3>
                </div>
                <div className="edit_eat__body">
                    {productListPreview}
                </div>
            </div>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        product_eaten: state.statistic.product_eaten,
    };
};

export default connect(mapStateToProps)(TypeMealPreview);