import React from 'react';
import {withRouter} from "react-router-dom";

class SportPreview extends React.Component {
    state = {  }

    openProductCard = () => {
        this.props.history.push(`/sport/id-${this.props.sport.id}`);
    }

    render() { 
        return ( 
            <div className="product_preview" onClick={this.openProductCard}>
                <p className="product_preview__title">{this.props.sport.name}</p>
                <div className="product_preview__content">
                    <span className="product_preview__text">{this.props.sport.time} min</span>
                    <span className="product_preview__text">{this.props.sport.cal} </span>
                </div>
            </div>
        );
    }
}
 
export default withRouter(SportPreview);