import React from 'react';

class Loader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {}
  }
  render() {
    return (
      <div className="loader">
        <div className="loader__spin">
          <span className="loader__circle"></span>
        </div>
      </div>
    );
  }
}

export default Loader;
