// import Xhr from "../../app/components/Xhr";
import ACTIONS from "../constants/actions";

export const product_eaten_add = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.PRODUCT_EATEN.ADD,
		  payload: action
    });
};
export const product_eaten_remove = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.PRODUCT_EATEN.REMOVE,
		  payload: action
    });
};
export const sport_add = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.SPORTS.SPORTS_ADD,
		  payload: action
    });
};
export const sport_remove = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.SPORTS.REMOVE,
		  payload: action
    });
};
