import React from 'react';
import { withRouter } from "react-router-dom";
import { connect } from 'react-redux';
import { fetchProdAll } from '../../redux/loadData/fetchTunk';
import Loader from '../components/Loader';
import ProductPreview from '../components/ProductPreview';
import FilterPrevList from './FilterPrevList';

class ProductsPrevList extends React.Component {
    // componentWillReceiveProps = (newProps) => { console.log('--ProductsPrevList- componentWillReceiveProps -'); };
    // componentWillUpdate = () => { console.log('--ProductsPrevList- componentWillUpdate'); };
    // componentDidUpdate = (oldProps, oldState) => { console.log('--ProductsPrevList- componentDidUpdate'); };
    // componentWillMount = () => { console.log('--ProductsPrevList- componentWillMount'); };
    // componentWillUnmount = () => { console.log('--ProductsPrevList- componentWillUnmount'); };
    state = {
        filter: {
            filterBy: 'calorie',
            direction: 'up'
        }
    }
    componentWillMount() {
        console.log('--ProductsPrevList- componentDidMount');
        const productsLoading = !!Object.keys(this.props.products.productsList).length;

        if (!productsLoading) {
            this.props.dispatch(fetchProdAll(this.props.dispatch));
        }
    }

    filterCall = (filter) => {
        this.setState({
            filter: filter
        });
    }


    render() {
        const productsLoading = !!Object.keys(this.props.products.productsList).length;

        if (!productsLoading) {
            return <Loader />
        }

        const { productsList } = this.props.products;
        const typeList = this.props.match.params.type;
        const products = productsList[typeList];
        let {filterBy, direction} = this.state.filter;

        if (direction === 'up') {
            products.sort((a, b) => {
                return +b[filterBy] - +a[filterBy];
            });
        } else {
            products.sort((a, b) => {
                return +a[filterBy] - +b[filterBy];
            });
        }

        
        let productsItemList = products.map((prod, key) => {
            return <ProductPreview key={key} product={prod}></ProductPreview>
        });

        return (
            <span>
                <span>
                    <FilterPrevList filterCall={this.filterCall}></FilterPrevList>
                </span>
                {productsItemList}
            </span>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        products: state.loadData.products,
    };
};

export default withRouter(connect(mapStateToProps)(ProductsPrevList));