import { saveInfoInStore } from './actions';

export const saveAndActivate = (info, diet) => (dispatch, getState) => {

    const USERS_INFO = 'USERS_INFO';
    const USERS_ACTIVE = 'USERS_ACTIVE';
    const USER_DEFERENT_PROGRAMM_NAME = 'definitionProgramm';
    let users = [];
    let newUser = {
        info,
        diet
    }
    let id = Math.random() * 100
    newUser.info.id = id;

    if (localStorage.getItem(USERS_INFO)) {
        users = JSON.parse(localStorage.getItem(USERS_INFO));
        users.push(newUser);
        localStorage.setItem(USERS_INFO, JSON.stringify(users));
    } else {
        users = [newUser];
        localStorage.setItem(USERS_INFO, JSON.stringify(users));
    }

    localStorage.setItem(USERS_ACTIVE, JSON.stringify(newUser));
    localStorage.removeItem(USER_DEFERENT_PROGRAMM_NAME);

    dispatch(saveInfoInStore({ diet, info }));
}

export const userCheck = (data) => (dispatch, getState) => {
    const USERS_ACTIVE = 'USERS_ACTIVE';
    localStorage.setItem(USERS_ACTIVE, JSON.stringify({info: data.info, diet: data.diet}));
    dispatch(saveInfoInStore({ info: data.info, diet: data.diet }));
}