import React from 'react';
import RangeSliderHalf from '../components/rangeSlider/RangeSliderHalf'; 
import { withSetDefinitionProgramm } from './withSetDefinitionProgramm';
import { withRouter } from "react-router";


class UserHalf extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            next: false,
            half: 70
        }
    }

    componentWillUnmount = () => {console.log('componentWillUnmount')};
    componentDidMount = () => {
        console.log('componentDidMount');

        this.props.gettDefirent('half');
    };

    componentDidUpdate = (oldProps, oldState) => { 
        console.log('componentDidUpdate '); 

        if (!this.state.next) {
            this.setState({
                next: true,
                half: this.props.half,
            });
        }
    };

    userHalf = (half) => {
        this.setState({
            half,
            next: true
        });
    }

    userNextStep = () => {
        this.props.history.push("/user/halftobe");
        this.props.setDefirent('half', this.state.half);
    }

    render() { 
        return ( 
            <div className="user_half user_wr">
                <h2 className="user_wr__title "> </h2>
                <div className="user_half__body">
                    <RangeSliderHalf half={this.state.half} key={this.state.half} userHalf={this.userHalf}></RangeSliderHalf>
                </div>
                <div className="user_half__footer">
                    <button className={`btn btn_color1 `} onClick={this.userNextStep} ></button>
                </div>
            </div>
         );
    }
}
 
export default withRouter(withSetDefinitionProgramm()(UserHalf));