import ACTIONS from "../constants/actions";

const initialState = {
    fetch: false,
    products: {
        productsList: {},
        productType: null,
    },
    sports: [],
   
}

export default (state = initialState, action) => {
    switch (action.type) {
        case ACTIONS.PRODUCTS.SET_PRODUCTS:
            return {
                ...state,
                products: {
                    ...state.products,
                    productsList: action.payload
                }
            }
        case ACTIONS.PRODUCTS.SET_PRODUCTS_TYPE:
            return {
                ...state,
                products: {
                    ...state.products,
                    productType: action.payload
                }
            }

        default:
            return state
    }
}