// import Xhr from "../../app/components/Xhr";
import ACTIONS from "../constants/actions";

export const getAllProducts = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.PRODUCTS.GET_ALL,
		  payload: action
    });
};
export const setLoadProductType = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.PRODUCTS.SET_PRODUCTS_TYPE,
		  payload: action
    });
};
export const setResponsProducts = (action) => dispatch => {
    dispatch({
		  type : ACTIONS.PRODUCTS.SET_PRODUCTS,
		  payload: action
    });
};




