import React from 'react';
import {withRouter} from "react-router-dom";

class ProductPreview extends React.Component {
    state = {  }

    openProductCard = () => {
        this.props.history.push(`/product/id-${this.props.product.id}`);
    }

    render() { 
        return ( 
            <div className="product_preview" onClick={this.openProductCard}>
                <p className="product_preview__title">{this.props.product.name}</p>
                <div className="product_preview__content">
                    <span className="product_preview__text">{this.props.product.calorie} </span>
                    <span className="product_preview__text"> {this.props.product.protein} .</span>
                    <span className="product_preview__text"> {this.props.product.fats} .</span>
                    <span className="product_preview__text"> {this.props.product.carb} .</span>
                </div>
            </div>
        );
    }
}
 
export default withRouter(ProductPreview);