import React from 'react';
import axios from 'axios';
import { withRouter } from "react-router-dom";
import { connect } from 'react-redux';
import Loader from './Loader';
import GoBackButton from './GoBackButton';
import { sport_add } from '../../redux/statistic/actions';

class SportCard extends React.Component {
    state = {
        sports: null,
        sportAdded: false,
    }

    inputRef = React.createRef();


    componentWillMount() {
        console.log('--SportPrevList- componentDidMount');

        if (!this.state.sports) {
            axios.get('http://www.mocky.io/v2/5d0b83382f00007600e3f168?mocky-delay=1000ms')
                .then(function (response) {
                    return response.data
                })
                .then((data) => {
                    let sportsFromStore = this.props.sports;
                    let sportAdded = false;
                    let idSport = +this.props.match.params.id.split('-')[1];
                    sportsFromStore.forEach((sport) => {
                        if (+sport.id === idSport) {
                            sportAdded = true;
                        }
                    });

                    this.setState({
                        sports: data,
                        sportAdded
                    });
                });
        }
    }

    componentDidMount() {
        console.log('----ProductCard - componentDidMount')

    }

    componentDidUpdate(oldProps, oldState) {
        console.log('----ProductCard - componentDidUpdate')
    }

    openChangeWhereSave = () => {
        // this.setState({
        //     opensavecard: true
        // });
    }

    addSport = () => {
        let idSport = +this.props.match.params.id.split('-')[1];
        let sportFind = {};
        let { sports } = this.state;

        sports.forEach((sport) => {
            if (+sport.id === idSport) {
                sportFind = sport;
            }
        });
        let time = this.inputRef.current.value;
        let newCal = (sportFind.cal / sportFind.time) * time  ;
        let newTime = time
        sportFind.cal = newCal;
        sportFind.time = newTime;

        this.props.dispatch(sport_add(sportFind))
      
        this.props.history.push(`/diary`);
    }

    removeSport = () => {
        // let idProd = this.state.product.id;
        // this.props.dispatch(product_eaten_remove_thunk(idProd));
        // this.props.history.push(`/diary`);
    }


    render() {
        let { sports, sportAdded } = this.state;

        if (!sports) {
            return <Loader />
        }

        let idSport = +this.props.match.params.id.split('-')[1];
        let sportFind = {};

        sports.forEach((sport) => {
            if (+sport.id === idSport) {
                sportFind = sport;
            }
        });

        

        // const { productAdded, product } = this.state;

        return (
            <div className="product_full">
                <GoBackButton></GoBackButton>
                <div className="product_full__header">
                    <h2 className="product_full__title">{sportFind.name}</h2>
                </div>
                <div>
                    {!sportAdded && <button onClick={this.addSport}></button>}
                    {sportAdded && <button></button>}
                    
                </div>
                <div className="product_full__body">
                    <input type="number" placeholder="30 min" defaultValue='30' ref={this.inputRef}></input> <span>min.</span>
                </div>
                <div className="product_full__footer">
                    <span className="product_preview__text">{sportFind.cal} </span>
                </div>
            </div>
        );
    }
}
const mapStateToProps = function (state) {
    return {
        sports: state.statistic.sports,
    };
};

export default withRouter(connect(mapStateToProps)(SportCard));